import React, { Component } from "react";
import { withRouter, useParams } from "react-router-dom";
import { Control, Errors, LocalForm } from "react-redux-form";
import Button from "@material-ui/core/Button";
import "./ResetPass.css";
import { CircularProgress } from "@material-ui/core";

var pass1 = "";
const required = (val) => val && val.length;
const validPass = (val) =>
  /^(?=.*[a-z])(?=.*\d)(?=.*[$@$!%*#?&<>^()])[a-z\d$@$!%*#?&<>^()]{8,}$/i.test(
    val
  );
const matched = (val) => val === pass1;
const store = (val) => {
  pass1 = val;
  return true;
};

class ResetPassComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hidden1: true,
      hidden2: true,
      resetSubmitted: false,
    };
    this.toggleShow1 = this.toggleShow1.bind(this);
    this.toggleShow2 = this.toggleShow2.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onSuccessReset = this.onSuccessReset.bind(this);
    this.onFailedReset = this.onFailedReset.bind(this);
  }

  toggleShow1() {
    this.setState({ hidden1: !this.state.hidden1 });
  }

  toggleShow2() {
    this.setState({ hidden2: !this.state.hidden2 });
  }

  componentDidMount() {
    // this.props.verifyEmail(user_id, timestamp, signature, this.props.history);
  }

  handleSubmit(values) {
    this.setState({ resetSubmitted: true });
    // const search = this.props.location.search;
    // const params = new URLSearchParams(search);
    // const user_id = Number(params.get("user_id"));
    // const timestamp = Number(params.get("timestamp"));
    // const signature = params.get("signature");
    let arr = this.props.user.key.split("-");
    let uid = arr[0];
    let token = arr[1] + "-" + arr[2];
    this.props.resetPass(values.password1, uid, token, this.onSuccessReset);
  }

  onSuccessReset() {
    this.setState({ resetSubmitted: false });
    this.props.history.push("/");
  }

  render() {
    return (
      <div className="container resetpass">
        <div className="reset-block">
          <h4>Reset Your password</h4>
          <hr />
          <div>
            <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
              <div className="row form-group">
                <div className="col col-12">
                  <label htmlFor="password1">New Password</label>
                </div>
                <div className="col col-12">
                  <div className="border password d-flex">
                    <Control
                      model=".password1"
                      id="password1"
                      name="password1"
                      placeholder="password"
                      md={10}
                      className="form-control"
                      style={{ border: "none", height: 50 }}
                      type={this.state.hidden1 ? "password" : "text"}
                      validators={{
                        required,
                        validPass,
                        store,
                      }}
                    />
                    <span
                      role="button"
                      tabIndex={0}
                      onClick={this.toggleShow1}
                      onKeyDown={this.toggleShow1}
                      style={{ outline: "none", fontSize: 20 }}
                      md={2}
                      className={
                        this.state.hidden1
                          ? "fa fa-eye pt-3 pr-2"
                          : "fa fa-eye-slash pt-3 pr-2"
                      }
                      id="togglePassword1"
                    ></span>
                  </div>
                  <Errors
                    className="text-danger error"
                    model=".password1"
                    show="touched"
                    messages={{
                      required: "Required ",
                      validPass:
                        "Must contain at least one numeric and special character",
                    }}
                  />
                </div>
              </div>
              <div className="row form-group">
                <div className="col col-12">
                  <label htmlFor="password2">Confirm Password</label>
                </div>
                <div className="col col-12">
                  <div className="border password d-flex">
                    <Control
                      model=".password2"
                      id="password2"
                      name="password2"
                      placeholder="Confirm Password"
                      md={10}
                      className="form-control"
                      style={{ border: "none", height: 50 }}
                      type={this.state.hidden2 ? "password" : "text"}
                      validators={{
                        required,
                        matched,
                      }}
                    />
                    <span
                      role="button"
                      tabIndex={0}
                      onClick={this.toggleShow2}
                      onKeyDown={this.toggleShow2}
                      style={{ outline: "none", fontSize: 20 }}
                      md={2}
                      className={
                        this.state.hidden2
                          ? "fa fa-eye pt-3 pr-2"
                          : "fa fa-eye-slash pt-3 pr-2"
                      }
                      id="togglePassword"
                    ></span>
                  </div>
                  <Errors
                    className="text-danger error"
                    model=".password2"
                    show="touched"
                    messages={{
                      required: "Required",
                      matched: "password does not match",
                    }}
                  />
                </div>
              </div>
              <div className="row form-group">
                <div className="col col-12">
                  <Button
                    variant="contained"
                    type="submit"
                    fullWidth={true}
                    style={{
                      backgroundColor: "#09bfad",
                      color: "white",
                      outline: "none",
                      height: 50,
                    }}
                  >
                    {this.state.resetSubmitted ? (
                      <CircularProgress style={{ color: "white" }} />
                    ) : (
                      "Submit"
                    )}
                  </Button>
                </div>
              </div>
            </LocalForm>
          </div>
        </div>
      </div>
    );
  }
}

function ResetPass(props) {
  const key = useParams();

  return (
    <div>
      <ResetPassComponent
        user={key}
        history={props.history}
        resetPass={props.resetPass}
      />
    </div>
  );
}

export default withRouter(ResetPass);
